
import { defineComponent, ref } from 'vue';
import { QuickData, QuickFetch } from '@/quick';
import { useRouter } from 'vue-router';

export default defineComponent({
  setup() {
    const qf = new QuickFetch();
    const qd = new QuickData();
    const account: any = ref('');
    const password: any = ref('');
    const router = useRouter();
    function submitAll() {
      qf.Dataset().AddObj('account', account).AddObj('password', password);
      qf.Url('auth/admin/login').Post(qf.GetDset()).then((res: any) => {
        if (res.message === 'done') {
          router.push('/');
          localStorage.sep5_admin_data = JSON.stringify(res.data.user);
          localStorage.sep5_admin_token = res.data.token;
        } else if (res.message === 'wrong_password') {
          qd.Alert('密碼錯誤');
        } else if (res.message === 'account_not_exist') {
          qd.Alert('帳號不存在');
        }
      });
      /*
      grecaptcha.ready(() => {
        grecaptcha.execute('6LdAJUghAAAAAI9lNFBSX3CLKQMSZOd-HJJrXOGY', { action: 'submit' }).then((token: any) => {
          // Add your logic to submit to your backend server here.
        });
      });
      */
    }
    return {
      account,
      password,
      submitAll,
    };
  },
});
